import React from "react";
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";
import React16 from "./components/react16";

const MyRouter = () => {
  return (
    <Router>
      <Switch>
        <Route path="/react16" component={React16}></Route>
        <Redirect to="react16"></Redirect>
      </Switch>
    </Router>
  );
};

export default MyRouter;
