import React, { useState } from "react";
import "./index.css";
import { Input, Form, Button, Empty, Space } from "antd";
import Cookie from "./components/cookie";
import Data from "./components/data";

function Base() {
  const [form] = Form.useForm();
  const [url, setUrl] = useState("");
  const [data, setData] = useState(null);

  const onFinish = () => {
    const { url, data, cookies } = form.getFieldsValue();
    let obj: any = {};
    if (cookies && cookies.length > 0) {
      cookies.forEach((item: any) => {
        document.cookie = `${item.key}=${item.value};domain=${item.domain}`;
      });
    }
    if (data && data.length > 0) {
      data.forEach((item: any) => {
        obj[item.key] = item.value;
      });
    }
    setUrl(url);
    setData(obj);
  };

  return (
    <>
      <div className="base-form">
        <h1>子应用开发环境模拟</h1>
        <Form form={form} onFinish={onFinish}>
          <Form.Item
            name="url"
            label="页面URL"
            rules={[
              {
                required: true,
                message: "不能为空！",
              },
            ]}
          >
            <Input style={{ width: "700px" }} />
          </Form.Item>
          <Cookie />
          <Data />
          <Space>
            <Button type="primary" htmlType="submit">
              查看数据
            </Button>
            <Button
              onClick={() => {
                form.resetFields();
                setData(null);
                setUrl("");
              }}
            >
              清空数据
            </Button>
          </Space>
        </Form>
      </div>
      <div className="base-content">
        {url ? (
          <micro-app
            url={url}
            name="test"
            data={data}
            baseroute="/react16"
          ></micro-app>
        ) : (
          <Empty
            image="https://gw.alipayobjects.com/zos/antfincdn/ZHrcdLPrvN/empty.svg"
            imageStyle={{
              height: 60,
            }}
            description={<span>请输入子应用url，查看内嵌效果</span>}
          ></Empty>
        )}
      </div>
    </>
  );
}

export default Base;
