import React from "react";
import { Input, Form, Button, Space } from "antd";
import { MinusCircleOutlined } from "@ant-design/icons";

const Cookie = () => {
  return (
    <Form.List name="cookies">
      {(fields, { add, remove }) => (
        <>
          {fields.map(({ key, name, ...restField }) => (
            <Space
              key={key}
              style={{
                display: "flex",
                marginBottom: 8,
              }}
              align="baseline"
            >
              <Form.Item
                label="key"
                style={{ width: "200px" }}
                {...restField}
                name={[name, "key"]}
                rules={[
                  {
                    required: true,
                    message: "Missing key",
                  },
                ]}
              >
                <Input placeholder="key" />
              </Form.Item>
              <Form.Item
                label="value"
                style={{ width: "200px" }}
                {...restField}
                name={[name, "value"]}
                rules={[
                  {
                    required: true,
                    message: "Missing value",
                  },
                ]}
              >
                <Input placeholder="value" />
              </Form.Item>
              <Form.Item
                label="domain"
                style={{ width: "200px" }}
                {...restField}
                name={[name, "domain"]}
              >
                <Input placeholder="domain" />
              </Form.Item>
              <MinusCircleOutlined onClick={() => remove(name)} />
            </Space>
          ))}
          <Form.Item>
            <Button
              type="primary"
              onClick={() => add()}
              style={{ width: "150px" }}
              block
              ghost
            >
              添加cookie
            </Button>
          </Form.Item>
        </>
      )}
    </Form.List>
  );
};

export default Cookie;
